import React from 'react'

import LoadingBar from '../LoadingBar'
import Scriptures from '../../firebase/Scriptures'
import SingleVerse from '../SingleVerse'
import Insights from '../../firebase/Insights'
import EditableInsight from '../EditableInsight'
import InsightEditorModal from '../InsightEditorModal'
import useFirebaseUser from '../../firebase/FirebaseUser'

const DEFAULT_INSIGHT = {
  source: {
    label: '',
    link: ''
  },
  deltaOps: []
}

function ChapterTextPage({ match: { params: { volume, book, chapter } } }) {
  const user = useFirebaseUser()

  const [verses, setVerses] = React.useState([])
  const [insights, setInsights] = React.useState(null)

  const [editing, setEditing] = React.useState(false)
  const [newInsight, setNewInsight] = React.useState(DEFAULT_INSIGHT)

  const scriptures = new Scriptures()
  const insightsStore = new Insights()

  React.useEffect(() => {
    scriptures.getVerses(volume, book, chapter, setVerses)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  React.useEffect(() => {
    insightsStore.getInsights([volume, book, chapter], setInsights)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editing])

  let chapterContent
  if (verses.length === 0) {
    chapterContent = <LoadingBar />
  }
  else {
    chapterContent = verses.map(v => <SingleVerse key={v.getHref()} verse={v} clickable={true}/>)
  }

  let insightContent
  if (insights === null) {
    insightContent = <LoadingBar />
  }
  else {
    insightContent = insights.map(i => <EditableInsight
      key={i.id}
      user={user}
      insight={i}
      toggle={toggleEditing}
      setInsight={setNewInsight}/>)
  }

  function toggleEditing() {
    setEditing(!editing)
  }

  function addNewInsight() {
    setNewInsight(DEFAULT_INSIGHT)
    toggleEditing()
  }

  return (
    <div>
      {editing ? <InsightEditorModal
        editing={editing}
        content={[volume, book, chapter]}
        insight={newInsight}
        setInsight={setNewInsight}
        toggle={toggleEditing} /> : null}
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4 className="text-capitalize">{book.replace(/_/g, ' ')} {chapter}</h4>
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4 className="text-capitalize float-left">chapter insights</h4>
          {user.status === 'LOGGED_IN' && <button className="btn btn-success float-right" onClick={addNewInsight}>Add Insight</button>}
          <div className="clearfix"></div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <hr />
          {chapterContent}
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12">
          <hr/>
          <ol>
            {insightContent}
          </ol>
        </div>
      </div>
    </div>
  )
}

export default ChapterTextPage
