import firebase from 'firebase/app'
import firebaseApp from './FirebaseConfig'

import 'firebase/firestore'
import Verse from '../model/Verse'

const db = firebase.firestore(firebaseApp)

let volumes = []
let books = {}
let chapterCounts = {}
let verseCounts = {}
let verses = {}

function fetchVolumes(cb) {
  db.doc('scriptures/index').get().then(function (doc) {
    volumes = doc.data()['volumes']
    cb(volumes)
  }).catch(function (err) {
    console.error('Error fetching volumes: ', err)
  })
}

function fetchBooks(volume, cb) {
  db.doc('scriptures/index/volumes/' + volume).get().then(function (doc) {
    books[volume] = doc.data()['books']
    cb(books[volume])
  }).catch(function (err) {
    console.error('Error fetching books for volume: ', volume, err)
  })
}

function fetchChapterCount(volume, book, cb) {
  db.doc('scriptures/index/volumes/' + volume + '/books/' + book).get().then(function (doc) {
    const chapterCount = doc.data()['chapters']
    chapterCounts[volume] = {}
    chapterCounts[volume][book] = chapterCount
    cb(chapterCount)
  }).catch(function (err) {
    console.error('Error fetching chapter count for ' + volume + ', ' + book, err)
  })
}

function fetchVerseCount(volume, book, chapter, cb) {
  db.doc('scriptures/index/volumes/' + volume + '/books/' + book + '/chapters/' + chapter).get().then(function (doc) {
    const verseCount = doc.data()['verses']
    verseCounts[volume] = {}
    verseCounts[volume][book] = {}
    verseCounts[volume][book][chapter.toString()] = verseCount
    cb(verseCount)
  }).catch(function (err) {
    console.error('Error fetching verse count for ' + volume + ', ' + book + ', chapter ' + chapter, err)
  })
}

function fetchVerses(volume, book, chapter, cb) {
  if (verses[volume] === undefined) verses[volume] = {}
  if (verses[volume][book] === undefined) verses[volume][book] = {}
  if (verses[volume][book][chapter] === undefined) verses[volume][book][chapter] = {}

  db.collection('scriptures/index/volumes/' + volume + '/books/' + book + '/chapters/' + chapter + '/verses').get().then(function (querySnapshot) {
    querySnapshot.docs.forEach(function (doc) {
      verses[volume][book][chapter][doc.id] = doc.data()['text']
    })

    cb(buildVerseList(volume, book, chapter))
  }).catch(function (err) {
    console.error('Error fetching verses for ' + volume + ', ' + book + ', chapter ' + chapter, err)
  })
}

function buildVerseList(volume, book, chapter) {
  return Object.keys(verses[volume][book][chapter]).map(function (n) {
    return new Verse(volume, book, chapter, n, verses[volume][book][chapter][n])
  })
}

class Scriptures {

  getVolumes(cb) {
    if (volumes.length === 0) {
      fetchVolumes(cb)
    }
    else {
      cb(volumes)
    }
  }

  getBooks(volume, cb) {
    if (books[volume] === undefined || books[volume].length === 0) {
      fetchBooks(volume, cb)
    }
    else {
      cb(books[volume])
    }
  }

  getChapterCount(volume, book, cb) {
    if (chapterCounts[volume] === undefined || chapterCounts[volume][book] === undefined) {
      fetchChapterCount(volume, book, cb)
    }
    else {
      cb(chapterCounts[volume][book])
    }
  }

  getVerseCount(volume, book, chapter, cb) {
    if (verseCounts[volume] === undefined ||
      verseCounts[volume][book] === undefined ||
      verseCounts[volume][book][chapter] === undefined) {
      fetchVerseCount(volume, book, chapter, cb)
    }
    else {
      cb(verseCounts[volume][book][chapter])
    }
  }

  getVerses(volume, book, chapter, cb) {
    if (verses[volume] === undefined ||
      verses[volume][book] === undefined ||
      verses[volume][book][chapter] === undefined) {
      fetchVerses(volume, book, chapter, cb)
    }
    else {
      cb(buildVerseList(volume, book, chapter))
    }
  }

  getVerse(volume, book, chapter, verse, cb) {
    this.getVerses(volume, book, chapter, function (verseList) {
      cb(verseList[verse - 1])
    })
  }
}

export default Scriptures
